import React, { useState } from "react";
import "./portfolio.css";
import Menu from "./Menu";

const Portfolio = () => {
  const [items, setItems] = useState(Menu);
  const filterItem = (categoryItem) => {
    const updatedItems = Menu.filter((curElem) => {
      return curElem.category === categoryItem;
    });

    setItems(updatedItems);
  };

  return (
    <section className="work container section" >
      <h2 className="section__title" id="projects">Projects</h2>

      <div className="work__filters">
        <span className="work__item" onClick={() => setItems(Menu)}>  
          All
        </span>
        <span className="work__item" onClick={() => filterItem("Web Dev")}>
          Web Development
        </span>
        <span className="work__item" onClick={() => filterItem("ML")}>
          Machine Learning
        </span>
        <span className="work__item" onClick={() => filterItem("Backend Dev")}>
          Backend Development
        </span>
      </div>

      <div className="work__container grid">
        {items.map((elem) => {
          const { id, image, title, category, url, description } = elem;
          return (
            <div className="work__card" key={id}>
              <div className="work__thumbnail">
                <img src={image} alt="" className="work__img" />
                <div className="work__mask"></div>
              </div>
              
              <span className="work__category">{category}</span>
              <h3 className="work__title">{title}</h3>
              <i className="work__description">{description}</i>
              <a href={url} target="_blank" rel="noopener noreferrer" className="work__button">
                <i className="icon-link work__button-icon"></i>
              </a>
            </div>
          );
        })}
      </div>
    </section>
  );
};

export default Portfolio;
